import React from "react";
import {
  Container,
  Button,
  Form,
  Row,
  Col,
  ProgressBar,
} from "react-bootstrap";
import { Link } from "react-router-dom";

import Alert from "./UI/Alert";

import classes from "./Content.module.css";

import sectoren from "./arrays/sectoren";
import leeftijden from "./arrays/leeftijden";
import vraag1 from "./arrays/vraag1";

const Content = (props) => {
  const {
    stage,
    clicked,
    changed,
    answers,
    length,
    alertMessage,
    email,
    alertColor,
  } = props;

  let title = (
    <div>
      <h1>Vraag {stage}</h1>
    </div>
  );
  let content = null;
  let buttons = (
    <div>
      <Button onClick={clicked} variant="secondary" name="vorige">
        Vorige
      </Button>
      <Button onClick={clicked} variant="secondary" name="volgende">
        Volgende
      </Button>
    </div>
  );
  let radios = [];

  switch (stage) {
    case 0:
      title = <h1>Meten is weten!</h1>;
      content = (
        <div>
          <p>Deze poll bestaat uit slechts zes vragen, en is volledig anoniem.</p>
          <p>
            Je kunt op elk moment op 'vorige' drukken om je antwoorden aan te
            passen. Je kunt de poll slechts één keer invullen.
          </p>
          <br />
        </div>
      );
      buttons = (
        <Button onClick={clicked} variant="secondary" name="start">
          Start
        </Button>
      );
      break;
    case 1:
      content = (
        <div>
          <p>
            Hoe groot acht je de kans dat je het Covid-19 virus hebt (gehad)?
          </p>

          <Form.Group as={Row} onChange={changed}>
            <Col sm={10}>
              {vraag1.map((radio, index) => {
                return (
                  <Form.Check
                    type="radio"
                    label={radio}
                    name={stage}
                    value={index}
                    checked={answers[stage] === index}
                    id={index + 1}
                    key={index}
                  />
                );
              })}
            </Col>
          </Form.Group>
        </div>
      );
      break;
    case 2:
      radios = [
        "1. Geen vertrouwen",
        "2. Weinig vertrouwen",
        "3. Redelijk veel vertrouwen",
        "4. Veel vertrouwen",
        "5. Heel veel vertrouwen",
        "Anders / Ik weet het niet",
      ];
      content = (
        <div>
          <p>
            Hoeveel vertrouwen heb jij op dit moment in de aanpak van de
            overheid om het virus te bestrijden, en tegelijkertijd de
            economische schade zo veel mogelijk te beperken?
          </p>

          <Form.Group as={Row} onChange={changed}>
            <Col sm={10}>
              {radios.map((radio, index) => {
                return (
                  <Form.Check
                    type="radio"
                    label={radio}
                    name={stage}
                    value={index}
                    checked={answers[stage] === index}
                    id={index + 1}
                    key={index}
                  />
                );
              })}
            </Col>
          </Form.Group>
        </div>
      );
      break;
    case 3:
      const kiesSector = sectoren.map((sector, index) => {
        return (
          <option key={index} value={index} selected={answers[stage] === index}>
            {sector}
          </option>
        );
      });
      content = (
        <div>
          <p>In welke sector ben je werkzaam?</p>
          <Form.Group as={Row} onChange={changed}>
            <Col sm={10}>
              <select onChange={changed} name={stage}>
                <option value="-1">Kies een sector...</option>
                {kiesSector}
              </select>
            </Col>
          </Form.Group>
        </div>
      );
      break;
    case 4:
      radios = [
        "Ik werk volledig thuis",
        "Ik werk op locatie (binnen)",
        "Ik werk op locatie (buiten)",
        "Ik werk soms thuis, soms op locatie",
        "Ik heb momenteel geen werk",
      ];
      content = (
        <div>
          <p>Hoe is je huidige werksituatie?</p>
          <Form.Group as={Row} onChange={changed}>
            <Col sm={10}>
              {radios.map((radio, index) => {
                return (
                  <Form.Check
                    type="radio"
                    label={radio}
                    name={stage}
                    value={index}
                    checked={answers[stage] === index}
                    id={index + 1}
                    key={index}
                  />
                );
              })}
            </Col>
          </Form.Group>
        </div>
      );
      break;
    case 5:
      radios = [
        "1. Niet",
        "2. Een klein beetje",
        "3. Een beetje",
        "4. Veel",
        "5. Heel veel",
        "Anders / Ik weet het niet",
      ];
      content = (
        <div>
          <p>
            In hoeverre heeft de hele situatie rondom het Coronavirus (denk aan
            de impact van de maatregelen, bang om ziek te worden, bang om
            inkomsten te verliezen etc) afgelopen week negatieve invloed gehad op je
            welzijn (mentaal)?
          </p>
          <Form.Group as={Row} onChange={changed}>
            <Col sm={10}>
              {radios.map((radio, index) => {
                return (
                  <Form.Check
                    type="radio"
                    label={radio}
                    name={stage}
                    value={index}
                    checked={answers[stage] === index}
                    id={index + 1}
                    key={index}
                  />
                );
              })}
            </Col>
          </Form.Group>
        </div>
      );
      break;

    case 6:
      radios = leeftijden.map((leeftijd, index) => {
        return (
          <Form.Check
            type="radio"
            label={leeftijd}
            name={stage}
            value={index}
            checked={answers[stage] === index}
            id={index + 1}
          />
        );
      });
      content = (
        <div>
          <p>Tot welke leeftijdsgroep behoor je?</p>
          <Form.Group as={Row} onChange={changed}>
            <Col sm={10}>{radios}</Col>
          </Form.Group>
        </div>
      );
      break;
    case 7:
      title = <h1>Bijna Klaar!</h1>;
      content = (
        <div>
          <p>
            Vul hier alleen nog je emailadres in. Hier wordt een versleutelde
            versie van opgeslagen in de databank. Je emailadres wordt op deze
            manier onleesbaar gemaakt, en je privacy 100% gewaarborgd.
          </p>
          <label htmlFor="email">Email:</label>
          <input type="email" onChange={changed} value={email} id="email" />
        </div>
      );
      buttons = (
        <div>
          <Button onClick={clicked} variant="secondary" name="vorige">
            Vorige
          </Button>
          <Button onClick={clicked} variant="secondary" name="verstuur">
            Verstuur
          </Button>
        </div>
      );
      break;
    case 8:
      title = <h1>Done!</h1>;
      content = <Link to="/stats">Bekijk de statistieken</Link>;
      buttons = null;
      break;
    default:
      content = null;
  }

  return (
    <Container className={classes.container}>
      {title}
      <p className={classes.content}>
        {stage === 0 ? null : (
          <ProgressBar
            animated
            variant="warning"
            now={(stage / length) * 100}
          />
        )}
        <Alert alertColor={alertColor}>{alertMessage}</Alert>
        {content}
      </p>
      {buttons}
    </Container>
  );
};

export default Content;
