import classes from "./Stats.module.css";
import axios from "axios";
import React, { useState, useEffect } from "react";
import { VictoryPie } from "victory";
import vraag1 from "./arrays/vraag1";
import Alert from "./UI/Alert";

const { stats, paragraph, wrapper } = classes;

const Stats = () => {
  const [total, setTotal] = useState();
  const [today, setToday] = useState();
  const [data, setData] = useState();

  useEffect(() => {
    console.log("effect");
    const apiCall = async () => {
      try {
        let poll = await axios.get("/polldata");
        const total = poll.data.numberOfDataSets;
        const today = poll.data.pollData.filter(
          (data) =>
            data.date === new Date(Date.now()).toISOString().slice(0, 10)
        ).length;

        poll = await axios.get("/polldata/question/1");
        const { answers } = poll.data;
        const newData = [];
        for (let i = 0; i < 6; i++) {
          let filter = answers.filter((answer) => answer === i).length;
          if (!filter == 0) {
            newData.push({ x: i + 1, y: filter });
          }
        }
        console.log(newData);
        setTotal(total);
        setToday(today);
        setData(newData);
      } catch (error) {
        console.log(error);
      }
    };
    apiCall();
  }, []);

  return (
    <div className={stats}>
      <Alert alertColor="warning">Hier wordt nog hard aan gewerkt!</Alert>
      <p className={paragraph}>
        Tot nu toe hebben<strong> {total} </strong>
        personen de enquete ingevuld.
      </p>
      <p className={paragraph}>
        Vandaag hebben<strong> {today} </strong>personen de enquete ingevuld.
      </p>
      <h5>
        Vraag 1: Hoe groot acht je de kans dat je het covid-19 virus hebt
        (gehad)?
      </h5>
      <div className={wrapper}>
        <VictoryPie
          data={data}
          padding={100}
          animate={{
            duration: 100,
          }}
          style={{
            labels: {
              fontSize: 20,
            },
          }}
        />
        <div>
          {vraag1.map((el) => {
            return <h6>{el}</h6>;
          })}
        </div>
      </div>
    </div>
  );
};

export default Stats;
